interface SidebarProps {
  collapsed: boolean;
}

export function Sidebar({ collapsed }: SidebarProps) {
  const pathname = usePathname();
  const [openDropdowns, setOpenDropdowns] = useState<Record<string, boolean>>({
    "ุฅุฏุงุฑุฉ ุงููุณุชุฎุฏููู": pathname?.startsWith("/user-management") || false,
  });

  const toggleDropdown = (label: string) => {
    setOpenDropdowns((prev) => ({
      ...prev,
      [label]: !prev[label],
    }));
  };

  const isActive = (href?: string) => {
    if (!href) return false;
    return pathname === href;
  };

  const isParentActive = (subItems?: SubItem[]) => {
    if (!subItems) return false;
    return subItems.some((item) => pathname === item.href);
  };

  return (
    <aside
      className={`bg-white min-h-screen border-l border-gray-200 shadow-sm transition-all duration-300 ${
        collapsed ? "w-16" : "w-64"
      }`}
    >
      <nav className="py-4">
        {menuItems.map((item, index) => (
          <div key={index}>
            {item.hasDropdown ? (
              <button
                onClick={() => toggleDropdown(item.label)}
                className={`w-full flex items-center gap-3 px-4 py-2.5 text-sm transition-colors ${
                  isParentActive(item.subItems)
                    ? "bg-blue-50 text-blue-600 border-l-4 border-blue-600"
                    : "text-gray-600 hover:bg-gray-50"
                }`}
                title={collapsed ? item.label : undefined}
              >
                <item.icon className="w-5 h-5 flex-shrink-0" />
                {!collapsed && (
                  <>
                    <span className="flex items-center gap-1 flex-1 text-right">
                      {item.hasEmoji && <span>๐๏ธ</span>}
                      {item.label}
                    </span>
                    <ChevronDown
                      className={`w-4 h-4 transition-transform duration-200 ${
                        openDropdowns[item.label] ? "rotate-180" : ""
                      }`}
                    />
                  </>
                )}
              </button>
            ) : (
              <Link
                href={item.href || "#"}
                className={`flex items-center gap-3 px-4 py-2.5 text-sm transition-colors ${
                  isActive(item.href) || pathname === item.href
                    ? "bg-blue-50 text-blue-600 border-l-4 border-blue-600"
                    : "text-gray-600 hover:bg-gray-50"
                }`}
                title={collapsed ? item.label : undefined}
              >
                <item.icon className="w-5 h-5 flex-shrink-0" />
                {!collapsed && (
                  <>
                    <span className="flex items-center gap-1 flex-1">
                      {item.hasEmoji && <span>๐๏ธ</span>}
                      {item.label}
                    </span>
                    <ChevronLeft className="w-4 h-4" />
                  </>
                )}
              </Link>
            )}
            {item.hasDropdown &&
              item.subItems &&
              openDropdowns[item.label] &&
              !collapsed && (
                <div className="bg-gray-50 border-r-2 border-gray-200">
                  {item.subItems.map((subItem, subIndex) => (
                    <Link
                      key={subIndex}
                      href={subItem.href}
                      className={`block px-10 py-2 text-sm transition-colors ${
                        pathname === subItem.href
                          ? "bg-blue-100 text-blue-600 font-medium"
                          : "text-gray-600 hover:bg-gray-100 hover:text-blue-600"
                      }`}
                    >
                      {subItem.label}
                    </Link>
                  ))}
                </div>
              )}
          </div>
        ))}
      </nav>
    </aside>
  );
}
